/* -- Reset -- */

* {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, h5, h6, p, div, table, tr, td, caption {
margin: 0;
padding: 0;
}

body { font-size: 16px; }

p { font-size: 1em; }

pre {
  font-size: 0.9em;
}

h1 { font-size: 2em; }
h2 { font-size: 1.6em; }
h3 { font-size: 1.4em; }
h4 { font-size: 1.2em; }
h5 { font-size: 1em; }
h6 { font-size: 0.8em; }
